// Title
.title {
  .secondary {
    width: fit-content;
    padding: 0 5px;

    font-family: $ff-secondary;
    font-size: $f-md;

    margin: 0 0 $d-mini 0;

    background: linear-gradient(to right, $c-purple, $c-teal);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    background-clip: text;

    opacity: 0;
    transform: translateY(30px);
    @include transition(0.2s, 0.1s);
  }
}

// Skills
.skills {
  width: 100%;
  height: 100%;

  position: absolute;
  opacity: 0.03;

  pointer-events: none;
}

.skills-list {
  width: 100vh;

  position: absolute;
  top: 50%;
  left: 100%;
  transform: translate(-68%, -50%) rotate(90deg);
}

.skills-item {
  margin-bottom: $d-mini;

  font-family: $ff-primary;
  text-transform: uppercase;
  color: $c-white;

  opacity: 0;
  transform: translateX(-30px);
}
.skills-item-1 {
  font-size: 4rem;
  @include transition(0.2s, 0.2s);
}
.skills-item-2 {
  font-size: 2.5rem;
  @include transition(0.2s, 0.3s);
}
.skills-item-3 {
  font-size: 2.5rem;
  @include transition(0.2s, 0.4s);
}
.skills-item-4 {
  font-size: 2rem;
  @include transition(0.2s, 0.5s);
}

// Queries
@include media(546px) {
  .title {
    .secondary-box {
      display: flex;
      justify-content: flex-end;
    }
    .secondary {
      margin-bottom: $d-sm;
    }
  }

  .skills-list {
    width: 100%;

    top: 0;
    left: 0;
    right: 0;

    transform: translate(-0%, -0%) rotate(0deg);
  }

  .skills-item {
    text-align: right;
  }
  .skills-item-1 {
    font-size: 12vw;
  }
  .skills-item-2 {
    font-size: 7vw;
  }
  .skills-item-3 {
    font-size: 7vw;
  }
  .skills-item-4 {
    font-size: 6vw;
  }
}
