body {
  background: #212121;
  font-family: "Roboto", sans-serif; }

a {
  color: #f0f0f0; }

::selection {
  background-color: #319795;
  color: #4c51bf; }

.nav {
  width: 100%;
  height: 5rem;
  position: absolute; }

.nav-list {
  width: 60%;
  justify-content: space-between; }

.nav-item {
  padding: 0.5rem;
  position: relative; }
  .nav-item .icon {
    font-size: 1.5rem; }
  .nav-item .square {
    width: 45px;
    height: 45px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 1px solid #f0f0f0;
    border-radius: 5px;
    opacity: 0; }
  .nav-item:hover .square {
    opacity: 1; }

.page-active .square {
  opacity: 0;
  animation: pageActiveIcon 0.3s ease-out 1s forwards; }

.nav-item-1 {
  animation: navIcons 0.3s ease-out 0.2s forwards; }

.nav-item-2 {
  animation: navIcons 0.3s ease-out 0.3s forwards; }

.nav-item-3 {
  animation: navIcons 0.3s ease-out 0.4s forwards; }

@media only screen and (min-width: 768px) {
  .nav {
    justify-content: flex-end;
    padding: 0 5vw; }
  .nav-list {
    width: 40%; } }

@media only screen and (min-width: 1024px) {
  .nav-list {
    max-width: 20%; } }

.title .secondary {
  width: fit-content;
  padding: 0 5px;
  font-family: "Romanesco", cursive;
  font-size: 2rem;
  margin: 0 0 0.5rem 0;
  background: linear-gradient(to right, #4c51bf, #319795);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  opacity: 0;
  transform: translateY(30px);
  transition: 0.2s ease-out 0.1s; }

.skills {
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 0.03;
  pointer-events: none; }

.skills-list {
  width: 100vh;
  position: absolute;
  top: 50%;
  left: 100%;
  transform: translate(-68%, -50%) rotate(90deg); }

.skills-item {
  margin-bottom: 0.5rem;
  font-family: "Anton", sans-serif;
  text-transform: uppercase;
  color: #f0f0f0;
  opacity: 0;
  transform: translateX(-30px); }

.skills-item-1 {
  font-size: 4rem;
  transition: 0.2s ease-out 0.2s; }

.skills-item-2 {
  font-size: 2.5rem;
  transition: 0.2s ease-out 0.3s; }

.skills-item-3 {
  font-size: 2.5rem;
  transition: 0.2s ease-out 0.4s; }

.skills-item-4 {
  font-size: 2rem;
  transition: 0.2s ease-out 0.5s; }

@media only screen and (min-width: 546px) {
  .title .secondary-box {
    display: flex;
    justify-content: flex-end; }
  .title .secondary {
    margin-bottom: 1rem; }
  .skills-list {
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    transform: translate(0%, 0%) rotate(0deg); }
  .skills-item {
    text-align: right; }
  .skills-item-1 {
    font-size: 12vw; }
  .skills-item-2 {
    font-size: 7vw; }
  .skills-item-3 {
    font-size: 7vw; }
  .skills-item-4 {
    font-size: 6vw; } }
